<template>
  <div class="vehicleMerge">
    <div class="vm-filters">
      <a-select style="width: 120px" placeholder="筛选阶段">
        <a-select-option value="lucy"> lucy </a-select-option>
      </a-select>
      <a-select style="width: 120px" placeholder="筛选部门">
        <a-select-option value="lucy"> lucy </a-select-option>
      </a-select>
      <a-select style="width: 120px" placeholder="筛选配置">
        <a-select-option value="lucy"> lucy </a-select-option>
      </a-select>
      <a-select style="width: 120px" placeholder="筛选车辆编号">
        <a-select-option value="lucy"> lucy </a-select-option>
      </a-select>
    </div>
    <ol class="vehicleMerge-list" ref="vmList">
      <li
        class="vm-li-1"
        draggable="false"
        v-for="(level1, index1) in mockData"
        :key="index1"
        :data-id="index1"
      >
        <div class="vm-1">
          <div class="vm-handle">
            <i class="css-icon icon-menu"></i>
          </div>
          <div class="vm-serialNumber">{{ index1 }}</div>
        </div>
        <div class="vm-2">
          <h3 class="vm-vehicleSerialNumber">
            <span class="vm-name1">{{ level1.name }}</span>
            <i class="css-icon icon-arrow-right"></i>
            <span class="vm-en">{{ level1.en }}</span>
          </h3>
          <h6 class="vm-configuration">
            {{ level1.config }}
          </h6>
        </div>
        <div class="vm-3">
          <ul class="vm-ul-2">
            <li
              class="vm-li-2"
              draggable="false"
              v-for="(level2, index2) in level1.children"
              :key="index2"
              :data-id="`${index1}-${index2}`"
            >
              <div class="vm-handle">
                <i class="css-icon icon-menu"></i>
              </div>
              <h2 class="vm-name2">{{ level2.name }}</h2>
              <ul class="vm-ul-3">
                <li
                  class="vm-li-3"
                  draggable="false"
                  v-for="(level3, index3) in level2.children"
                  :key="index3"
                  :data-id="`${index1}-${index2}-${index3}`"
                >
                  <div class="vm-handle">
                    <i class="css-icon icon-menu"></i>
                  </div>
                  <h3 class="vm-name3">{{ level3.name }}</h3>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </li>
    </ol>
  </div>
</template>

<script>
/* eslint-disable */
import mockData from './mockData'
import {
  getParentElement,
  getDragImage,
  mergeLevel3,
  mergeLevel2,
  mergeLevel1,
  StepsCache,
  getLevel,
} from './tool'
export default {
  name: 'VehicleMerge',
  data() {
    return {
      mockData,
    }
  },
  mounted() {
    this.initEvent()
    this.stepsCache = new StepsCache()
    this.stepsCache.add(this.mockData)
    this.initWebSocket()
  },
  beforeDestroy() {
    this.destroy()
    this.socket.close()
  },
  methods: {
    initWebSocket() {
      const socket = (this.socket = new WebSocket(
        'ws://' + window.location.hostname + ':8081'
      ))
      socket.addEventListener('open', () => {
        console.log('connected')
      })
      socket.addEventListener('error', () => {
        console.log('connection error')
      })
      socket.addEventListener('close', () => {
        console.log('closed')
      })
      socket.addEventListener('message', (event) => {
        const { data } = event
        console.log('有人操作了！', data)
        this.mockData = JSON.parse(data)
      })
    },
    redo() {
      this.mockData = this.stepsCache.redo()
      this.socket.send(JSON.stringify(this.mockData))
    },
    undo() {
      this.mockData = this.stepsCache.undo()
      this.socket.send(JSON.stringify(this.mockData))
      this.removeT()
    },
    sort() {
      const { dragged, dropzone, level, mockData, direction } = this
      const [o1, o2, o3] = dragged.dataset.id.split('-')
      const [t1, t2, t3] = dropzone.dataset.id.split('-')
      if (level === 3) {
        const [removedItem] = mockData[o1].children[o2].children.splice(o3, 1)
        const index = direction === 'left' ? t3 : t3 + 1
        mockData[t1].children[t2].children.splice(index, 0, removedItem)
      } else if (level === 2) {
        const [removedItem] = mockData[o1].children.splice(o2, 1)
        const index = direction === 'top' ? t2 : t2 + 1
        mockData[t1].children.splice(index, 0, removedItem)
      } else if (level === 1) {
        const [removedItem] = mockData.splice(o1, 1)
        const index = direction === 'top' ? t1 : t1 + 1
        mockData.splice(index, 0, removedItem)
      }
    },
    sortingTip(target, clientX, clientY) {
      const { offsetLeft, offsetTop } = target
      const { width, height } = target.getBoundingClientRect()
      const diffX = clientX - offsetLeft
      const diffY = clientY - offsetTop
      if (this.level === 3) {
        this.direction = diffX > width / 2 ? 'right' : 'left'
      } else {
        this.direction = diffY > height / 2 ? 'bottom' : 'top'
      }
    },
    merge(isCopy = false) {
      const { dragged, dropzone, level, mockData } = this
      let [o1, o2, o3] = dragged.dataset.id.split('-')
      let [t1, t2] = dropzone.dataset.id.split('-')
      o1 *= 1
      o2 *= 1
      o3 *= 1
      t1 *= 1
      t2 *= 1
      switch (level) {
        case 3:
          mergeLevel3(mockData, o1, o2, o3, t1, t2, isCopy)
          break
        case 2:
          mergeLevel2(mockData, o1, o2, t1, t2, isCopy)
          break
        case 1:
          mergeLevel1(mockData, o1, t1, isCopy)
          break
      }
    },
    mousedown(event) {
      const { target, button } = event
      if (button !== 0) return
      const { className } = target
      const dragged = getParentElement(target, 'vm-li-')
      if (className.includes('vm-handle') || className.includes('icon-menu')) {
        this.vmHandle = 'sort'
        dragged.setAttribute('draggable', true)
      } else if (dragged.className.includes('vm-li-')) {
        this.vmHandle = 'drag'
        dragged.setAttribute('draggable', true)
      }
    },
    dragstart(event) {
      const { target, dataTransfer } = event
      if (!target || target.nodeType !== 1) return
      dataTransfer.effectAllowed = 'copyMove'
      this.dragged = target
      this.dropzone = null
      if (this.vmHandle === 'drag') {
        if (target.className.includes('vm-li-3')) {
          const { dragged } = this
          const [o1, o2, o3] = dragged.dataset.id.split('-')
          const data = this.mockData[o1].children[o2]
          const name2 = data.name
          const name3 = data.children[o3].name
          const dragImage = (this.dragImage = getDragImage(name2, name3))
          const { width, height } = dragImage.getBoundingClientRect()
          dataTransfer.setDragImage(dragImage, width / 2, height / 2)
        }
      }
    },
    dragend() {
      const { dragImage } = this
      dragImage && dragImage.remove()
    },
    dragover(event) {
      if (!this.dragged) return
      const { target, dataTransfer, clientX, clientY, ctrlKey } = event
      const level = (this.level = getLevel(this.dragged))
      const { vmHandle } = this
      if (vmHandle === 'drag') {
        switch (level) {
          case 3:
          case 2:
            this.dropzone = getParentElement(target, 'vm-li-2')
            break
          case 1:
            this.dropzone = getParentElement(target, 'vm-li-1')
            break
        }
        if (level === 1) {
          if (
            this.dropzone.tagName === 'LI' &&
            !this.dropzone.contains(this.dragged)
          ) {
            event.preventDefault()
            if (ctrlKey) {
              dataTransfer.dropEffect = 'copy'
            } else {
              dataTransfer.dropEffect = 'move'
            }
          }
        } else if (level === 2 || level === 3) {
          if (this.dropzone.tagName === 'LI') {
            const vmli1 = getParentElement(this.dropzone, 'vm-li-1')
            if (!vmli1.contains(this.dragged)) {
              event.preventDefault()
              if (ctrlKey) {
                dataTransfer.dropEffect = 'copy'
              } else {
                dataTransfer.dropEffect = 'move'
              }
            }
          }
        }
      } else if (vmHandle === 'sort') {
        const vmul3 = this.dragged.parentNode
        const vmli3 = (this.dropzone = getParentElement(target, 'vm-li-'))
        if (
          vmul3.contains(vmli3) &&
          vmli3.tagName === 'LI' &&
          vmli3 !== this.dragged
        ) {
          event.preventDefault()
          dataTransfer.dropEffect = 'move'
          this.sortingTip(vmli3, clientX, clientY)
        }
      }
    },
    drop(event) {
      const { vmHandle } = this
      if (vmHandle === 'drag') {
        event.preventDefault()
        const isCopy = event.ctrlKey ? true : false
        this.merge(isCopy)
        const { dragImage } = this
        dragImage && dragImage.remove()
      } else if (vmHandle === 'sort') {
        this.sort()
      }
      this.stepsCache.add(this.mockData)
      this.socket.send(JSON.stringify(this.mockData))
    },
    keydown(event) {
      const { defaultPrevented, key, ctrlKey } = event
      if (defaultPrevented) return
      if (!ctrlKey) return
      switch (key) {
        case 'z':
          this.undo()
          break
        case 'r':
          this.redo()
          break
        case 'c':
        case 'x':
          this.copyOrCut(key)
          break
        case 'v':
          this.paste()
        default:
          return
      }
      event.preventDefault()
    },
    click(event) {
      const { target } = event
      this.ele_o && this.ele_o.classList.remove('vm-select')
      const ele_o = (this.ele_o = getParentElement(target, 'vm-li-'))
      if (ele_o.className.includes('vm-li-')) {
        ele_o.classList.add('vm-select')
      } else {
        this.ele_o = null
      }
    },
    copyOrCut(key) {
      if (!this.ele_o) return
      this.removeT()
      this.ele_t = this.ele_o
      this.removeO()
      const level = getLevel(this.ele_t)
      this.ele_t.classList.add(`vm-active${level}`)
      this.key = key
    },
    paste() {
      const { ele_o, ele_t } = this
      if (ele_o && ele_t) {
        const pEle = getParentElement(ele_o, 'vm-li-1')
        if (pEle.contains(ele_o) && pEle.contains(ele_t)) return
        this.dragged = ele_t
        this.dropzone = ele_o
        this.level = getLevel(this.dragged)
        const isCopy = this.key === 'c' ? true : false
        this.merge(isCopy)
        this.stepsCache.add(this.mockData)
        this.socket.send(JSON.stringify(this.mockData))
      }
    },
    dblclick(event) {
      const { target } = event
      if (!this.$refs.vmList.contains(target)) {
        this.removeO()
        this.removeT()
      }
    },
    removeT() {
      this.ele_t &&
        this.ele_t.classList.remove('vm-active3', 'vm-active2', 'vm-active1')
      this.ele_t = null
    },
    removeO() {
      this.ele_o && this.ele_o.classList.remove('vm-select')
      this.ele_o = null
    },
    initEvent() {
      document.addEventListener('dragstart', this.dragstart, false)
      document.addEventListener('dragend', this.dragend, false)
      document.addEventListener('dragover', this.dragover, false)
      document.addEventListener('drop', this.drop, false)
      document.addEventListener('mousedown', this.mousedown, false)
      document.addEventListener('keydown', this.keydown, true)
      document.addEventListener('click', this.click, false)
      document.addEventListener('dblclick', this.dblclick, false)
    },
    destroy() {
      document.removeEventListener('dragstart', this.dragstart, false)
      document.removeEventListener('dragend', this.dragend, false)
      document.removeEventListener('dragover', this.dragover, false)
      document.removeEventListener('drop', this.drop, false)
      document.removeEventListener('mousedown', this.mousedown, false)
      document.removeEventListener('keydown', this.keydown, true)
      document.removeEventListener('click', this.click, false)
      document.removeEventListener('dblclick', this.dblclick, false)
    },
  },
}
</script>

<style lang="less" src="./vehicleMerge.less" />