/* eslint-disable */
export const copyData = (data) => {
  return JSON.parse(JSON.stringify(data))
}

export const getParentElement = (target, cls) => {
  while (target.tagName !== 'BODY' && typeof target.className === 'string' && !target.className.includes(cls) && target.parentNode) {
    target = target.parentNode
  }
  return target
}

export const getDragImage = (name2, name3) => {
  document.body.insertAdjacentHTML('beforeend', `
    <div class="vm-li-2">
      <div class="vm-handle"><i class="css-icon icon-menu"></i></div>
      <h2 class="vm-name2">${name2}</h2>
      <ul class="vm-ul-3">
        <li draggable="true" data-id="1-0-0" class="vm-li-3">
          <div class="vm-handle"><i class="css-icon icon-menu"></i></div>
          <h3 class="vm-name3">${name3}</h3>
        </li>
      </ul>
    </div>`
  )
  return document.querySelector('body > .vm-li-2')
}

export const removeEmptyData3 = (data, o1, o2) => {
  const len3 = data[o1].children[o2].children.length
  if (len3 === 0) {
    data[o1].children.splice(o2, 1)
  }
  removeEmptyData2(data, o1)
}

export const removeEmptyData2 = (data, o1) => {
  const len2 = data[o1].children.length
  if (len2 === 0) {
    data.splice(o1, 1)
  }
}

export const mergeData2 = (data, t1, t2, removedItem) => {
  const data_l2 = data[t1].children
  const index2 = data_l2.findIndex(item => {
    return item.name === removedItem.name
  })
  if (index2 === -1) {
    data_l2.splice(t2 + 1, 0, removedItem)
  } else {
    const data_l3 = data_l2[index2].children
    const index3 = data_l3.findIndex(item => {
      return item.name === removedItem.children[0].name
    })
    if (index3 === -1) {
      const mergedData = data_l3.concat(removedItem.children)
      data_l2[index2].children = mergedData
    }
  }
}

export const mergeData1 = (data, t1, removedItem) => {
  const data_l2 = data[t1].children
  const removedArray = removedItem.children
  const len1 = removedArray.length
  const len2 = data_l2.length
  for (let i = 0; i < len1; i++) {
    const item1 = removedArray[i]
    let hasSome = false
    for (let j = 0; j < len2; j++) {
      const item2 = data_l2[j]
      if (item2.name === item1.name) {
        item2.children = item2.children.concat(item1.children)
        hasSome = true
        break
      }
    }
    if (!hasSome) {
      data_l2.push(item1)
    }
  }
}

export const mergeLevel3 = (data, o1, o2, o3, t1, t2, isCopy = false) => {
  let data_l2 = data[o1].children[o2]
  let data_l2_copy = copyData(data_l2)
  let removedItem = null
  if (isCopy) {
    removedItem = [copyData(data_l2.children[o3])]
  } else {
    removedItem = data_l2.children.splice(o3, 1)
  }
  data_l2_copy.children = removedItem
  mergeData2(data, t1, t2, data_l2_copy)
  removeEmptyData3(data, o1, o2)
}

export const mergeLevel2 = (data, o1, o2, t1, t2, isCopy = false) => {
  let removedItem = null
  if (isCopy) {
    removedItem = copyData(data[o1].children[o2])
  } else {
    removedItem = data[o1].children.splice(o2, 1)[0]
  }
  mergeData2(data, t1, t2, removedItem)
  removeEmptyData2(data, o1)
}

export const mergeLevel1 = (data, o1, t1, isCopy = false) => {
  let removedItem = null
  if (isCopy) {
    removedItem = copyData(data[o1])
    if (o1 < t1) {
      t1 += 1
    }
  } else {
    removedItem = data.splice(o1, 1)[0]
  }
  if (o1 < t1) {
    t1 -= 1
  }
  mergeData1(data, t1, removedItem)
}

export class StepsCache {
  constructor() {
    this.max = 11
    this.cache = []
    this.currentIndex = -1
  }

  add(data) {
    const { cache, max } = this
    const dataCopy = copyData(data)
    if (cache.length === max) {
      cache.shift()
      this.currentIndex--
    }
    if (this.currentIndex === -1) {
      this.currentIndex = 0
    } else {
      this.currentIndex++
    }
    cache[this.currentIndex] = dataCopy
    this.cache.length = this.currentIndex + 1
  }

  redo() {
    const { cache } = this
    const { length } = cache
    this.currentIndex += 1
    if (this.currentIndex === length) {
      this.currentIndex = length - 1
    }
    const res = cache[this.currentIndex]
    return copyData(res)
  }

  undo() {
    this.currentIndex -= 1
    if (this.currentIndex < 0) {
      this.currentIndex = 0
    }
    const res = this.cache[this.currentIndex]
    return copyData(res)
  }
}

export const getLevel = (target) => {
  return target.dataset.id.split('-').length
}
